footer {
  display: block;
}
footer .footer {
  background: #00bdbd;
}
footer .footer .container {
  padding: 40px 0;
}
footer .footer .container ul {
  margin: 0 auto;
  padding: 0;
  text-align: center;
  list-style-type: none;
}
footer .footer .container ul li {
  display: inline-block;
  text-align: center;
  background: #ffffff;
  color: #00bdbd;
  line-height: 42px;
  margin: 0 6px;
  width: 42px;
  height: 42px;
  border-radius: 50%;
}
footer .footer .container ul li:hover {
  background: #0a9f9f;
}
footer .footer .container ul li:hover a {
  color: #fff;
}
footer .footer .container ul li a {
  color: #00bdbd;
  font-size: 0.8rem;
  width: 42px;
  height: 42px;
}
footer .footer .container ul li a i {
  line-height: 42px;
  width: 42px;
  border-radius: 50%;
}
