.experience {
  background: #fff;
}
.experience .container h2 {
  color: #ff675f;
}
.experience .container h3 {
  color: #84868d;
}
.experience .container .info .job {
  text-align: right;
  margin-top: 3em;
}
.experience .container .info .job .year {
  width: 20%;
  text-align: right;
  display: inline-block;
  vertical-align: top;
  font-size: 0.9em;
}
.experience .container .info .job .year span {
  padding: 0.8em;
  color: #fff;
  display: inline-block;
}
.experience .container .info .job .year span.prev {
  background: #ff675f;
}
.experience .container .info .job .year span.after {
  background: #474d5d;
}
.experience .container .info .job .description {
  text-align: left;
  display: inline-block;
  width: 70%;
  padding-left: 8%;
}
.experience .container .info .job .description div h4 {
  margin: 0;
}
.experience .container .info .job .description div h5 {
  margin: 0.8em 0;
}
