header {
  padding: 0;
  margin: 0;
  height: 280px;
  background-image: url('../img/bg-header-blue.png');
  color: #fff;
  overflow: hidden;
  display: table;
  width: 100%;
}
header .container {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  padding: 0;
}
header .container .title h1 {
  font-size: 5em;
  line-height: 0;
  font-weight: 100;
}
header .container .title h2 {
  font-weight: 100;
}
nav {
  margin-top: 0px;
  position: relative;
  z-index: 15;
  background: #262b37;
}
nav .menu {
  margin: 0 auto;
  text-align: center;
  padding: 0;
  list-style: none;
}
nav .menu li {
  display: inline-block;
  padding: 22px 0;
}
nav .menu li a {
  color: #c3c8d6;
  padding: 22px 25px;
  line-height: 18px;
  text-align: center;
}
nav .menu li a:hover {
  color: #fff;
}
nav .menu li.active {
  background: #1b1f29;
  color: #c3c8d6;
}
