.about {
  background: #00bdbd;
  color: #fff;
}
.about .container .info .text {
  width: 64%;
  display: inline-block;
  vertical-align: middle;
  margin-right: 8%;
}
.about .container .info .text h4 {
  font-size: 1.5em;
  line-height: 30px;
  padding-bottom: 6px;
  font-weight: 200;
  margin: 0px;
}
.about .container .info .text h5 {
  font-size: 1.2em;
  line-height: 24px;
  padding-bottom: 6px;
  font-weight: 400;
  margin: 0px;
}
.about .container .info .text p {
  font-size: 0.9em;
  line-height: 21px;
  font-weight: 200;
  padding-bottom: 22px;
  margin: 0px;
}
.about .container .info .text a {
  font-size: 1em;
  font-weight: 700;
  background: #474d5d;
  padding: 16px 22px;
  display: inline-block;
  color: #fff;
}
.about .container .info .image {
  width: 27%;
  display: inline-block;
  vertical-align: middle;
}
.about .container .info .image img {
  width: 100%;
  border-radius: 50%;
}
@media (max-width: 1024px) {
  .about .container .info .text {
    width: 60%;
    margin-right: 10%;
  }
  .about .container .info .image {
    width: 27%;
  }
}
@media (max-width: 768px) {
  .about .container .info .text {
    width: 100%;
    margin-bottom: 5em;
    margin-right: 0;
  }
  .about .container .info .image {
    width: 100%;
    text-align: center;
  }
  .about .container .info .image img {
    width: 60%;
  }
}
