.education {
  background: #eeeeee;
  color: #474d5d;
}
.education .container h2 {
  color: #ff675f;
}
.education .container h3 {
  color: #84868d;
}
.education .container .info .career {
  text-align: right;
}
.education .container .info .career .year {
  width: 20%;
  text-align: center;
  display: inline-block;
  vertical-align: top;
  background: #474d5d;
  color: #FFFFFF;
  font-size: 0.9em;
  margin-top: 2em;
  padding: 0.3em 0;
}
.education .container .info .career .description {
  text-align: left;
  display: inline-block;
  width: 70%;
  padding-left: 8%;
}
.education .container .info .career .description div {
  background: #fff;
  border-bottom: solid 4px #e8e7e7;
  padding: 2em;
  position: relative;
}
.education .container .info .career .description div:before {
  content: "";
  width: 0;
  height: 0;
  border: 10px solid transparent;
  border-right: 10px solid #fff;
  left: -20px;
  position: absolute;
}
.education .container .info .career .description div h4 {
  margin: 0;
}
.education .container .info .career .description div h5 {
  margin: 0.8em 0;
}
.education .container .info .courses {
  text-align: right;
}
.education .container .info .courses .description {
  text-align: left;
  display: inline-block;
  width: 70%;
  padding-left: 8%;
}
.education .container .info .courses .description div {
  background: #fff;
  border-bottom: solid 4px #e8e7e7;
  padding: 2em;
  position: relative;
}
.education .container .info .courses .description div h4 {
  margin: 0;
}
