.contact {
  background: #474d5d;
  display: block;
}
.contact .container .form {
  min-height: 0.1em;
  width: 66%;
  display: inline-block;
  box-sizing: border-box;
  vertical-align: top;
  text-align: right;
}
.contact .container .form h4 {
  text-transform: uppercase;
  color: #fff;
  font-size: 1.2em;
}
.contact .container .form .g-recaptcha {
  margin: 0 calc(100% - 465px) 0 0;
  display: inline-block;
}
.contact .container .form input {
  background: #585f73;
  color: #93959c;
  padding: 15px 30px;
  border: none;
  margin-bottom: 30px;
  outline: none;
  font-style: italic;
  width: 48%;
  box-sizing: border-box;
}
.contact .container .form input.name {
  margin-right: 2%;
}
.contact .container .form input.submit {
  background: #ff675f;
  color: #fff;
  font-size: 16px;
  text-align: center;
  float: right;
  margin: 0;
  width: auto;
}
.contact .container .form textarea {
  background: #585f73;
  color: #93959c;
  padding: 15px 30px;
  border: none;
  margin-bottom: 40px;
  outline: none;
  height: 100px;
  font-style: italic;
  width: 98%;
  box-sizing: border-box;
}
.contact .container .details {
  min-height: 0.1em;
  padding-right: 1em;
  padding-left: 1em;
  width: 33%;
  display: inline-block;
  box-sizing: border-box;
  vertical-align: top;
}
.contact .container .details h4 {
  text-transform: uppercase;
  color: #fff;
  font-size: 1.2em;
}
.contact .container .details p {
  color: #93959c;
  font-size: 0.9em;
  line-height: 1.5em;
  margin-left: 2em;
}
.contact .container .details p i {
  padding-right: 15px;
}
.contact .container .details p a {
  color: #93959c;
}
.contact .container .details p a:hover {
  color: #a9abb0;
  text-decoration: none;
}
