.portfolio {
  background: #2bb3dd;
  color: #fff;
}
.portfolio .container {
  position: relative;
  -webkit-transition: all 1s ease;
  -moz-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
  overflow: hidden;
}
.portfolio .container .projects .filters ul {
  margin: 0 auto 30px auto;
  text-align: center;
  display: block;
}
.portfolio .container .projects .filters ul li {
  display: inline-block;
  margin: 5px 5px;
}
.portfolio .container .projects .filters ul li span {
  padding: 16px 18px;
  font-size: 13px;
  line-height: 15px;
  color: #ffffff;
  text-transform: uppercase;
  display: block;
  text-decoration: none;
  border: solid 1px #95d9ee;
  cursor: pointer;
  transition-duration: 0.2s;
  -webkit-transition-duration: 0.2s;
}
.portfolio .container .projects .filters ul li span.active {
  background: #15596e;
  border: solid 1px #15596e;
  color: #fff;
}
.portfolio .container .projects .project-list .project-item {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  width: 23%;
  margin: 1%;
  display: none;
  float: left;
  overflow: hidden;
}
.portfolio .container .projects .project-list .project-item .project-wrapper {
  overflow: hidden;
  position: relative !important;
  background: #666;
  cursor: pointer;
}
.portfolio .container .projects .project-list .project-item .project-wrapper img {
  max-width: 100%;
  position: relative;
  display: block;
}
.portfolio .container .projects .project-list .project-item .project-wrapper .label {
  position: absolute;
  width: 100%;
  height: 60px;
  bottom: -60px;
}
.portfolio .container .projects .project-list .project-item .project-wrapper .label .label-bg {
  background: #15596E;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.portfolio .container .projects .project-list .project-item .project-wrapper .label .label-text {
  color: #fff;
  position: relative;
  z-index: 500;
  padding: 10px 15px;
}
.portfolio .container .projects .project-list .project-item .project-wrapper .label .label-text .text-category {
  display: block;
  font-size: 9px;
}
